.Container{
    width: 650px;
    margin:4rem auto;
}
.Box{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgb(136, 51, 51);
    height: 650px;
    border-radius: 1rem;
}

.Place{
    margin: 0.5rem;
    width: 400px;
}

.Boton{
    color: blue;
    background-color: rgb(rgb(143, 44, 44), rgb(70, 121, 70), rgb(86, 86, 153));
    margin-bottom: 2rem;
}

.Boton:hover{
    cursor: pointer;
    background: rgb(191, 205, 235);

}
.Img_Perfil{
    margin: 2rem auto;
    width: 200px;
    height: 200px;
}

.Registro{
    width: 100px;
    height: 1.5rem;
    padding: 0.25rem;
    margin-bottom: 2rem;
    border-radius: 10px;
    border: none;
    font-size: medium;
}
.Registro:hover{
    cursor: pointer;
    background: rgb(191, 205, 235);
}

